<template>
  <v-app id="main" :style="style">
    <!-- <v-overlay :value="overlay" opacity=".9" z-index="10">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>-->

    <MainMenu v-if="showMenu" />
    <v-content>
      <Announcement v-if="showMenu"></Announcement>
      <v-row justify="space-between" align="start" no-gutters>
        <v-col cols="12" md="6">
          <Breadcrumbs v-if="hasBreadcrumbs"></Breadcrumbs>
        </v-col>
        <v-col cols="12" md="3" class="hidden-sm-and-down">
          <SocialMedia v-if="showMenu"></SocialMedia>
        </v-col>
      </v-row>
      <v-container grid-list-md fluid>
        <v-row justify="center" align="start" style="height:100%">
          <v-col cols="12" lg="9" xs12>
            <keep-alive>
              <router-view></router-view>
            </keep-alive>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
    <v-snackbar v-model="snackbar.showing" :timeout="4000" :color="snackbar.color">
      {{snackbar.text}}
      <v-btn text @click="snackbar.showing=false">Close</v-btn>
    </v-snackbar>
    <Footer v-if="showMenu" />
  </v-app>
</template>


<script>
import { mapActions, mapState } from "vuex";

import MainMenu from "@/components/layout/MainMenu.vue";
import Announcement from "@/components/layout/Announcement.vue";
import Breadcrumbs from "@/components/layout/Breadcrumbs.vue";
import SocialMedia from "@/components/layout/SocialMedia.vue";

import Footer from "@/components/layout/Footer.vue";

export default {
  name: "App",
  components: {
    MainMenu,
    Announcement,
    Breadcrumbs,
    SocialMedia,
    Footer
  },

  data: () => ({
    // overlay: false
  }),

  async created() {
    // this.overlay = true;
    await this.getConfigs();
    // this.overlay = false;
  },
  computed: {
    ...mapState(["snackbar"]),
    theme() {
      return this.$vuetify.theme.dark ? "dark" : "light";
    },

    style() {
      const Style = {
        background: this.$vuetify.theme.themes[this.theme].background,
        color: this.$vuetify.theme.themes[this.theme].color
      };

      return Style;
    },

    showMenu() {
      return this.$route.meta.showMenu;
    },
    hasBreadcrumbs: function() {
      let breadCrumbLength = 0;

      if (this.$route.meta.breadCrumbs) {
        breadCrumbLength = this.$route.meta.breadCrumbs.length;
      }

      return breadCrumbLength;
    }
  },

  methods: {
    ...mapActions(["getConfigs"])
  }
};
</script>


<style lang="sass">
@import '~vuetify/src/styles/styles.sass'

a
  text-decoration: none

.v-application a
  color: var(--v-info-darken2)
</style>
