const state = {
  groups: [],
};

const getters = {
  retrieveGroups: (state) => {
    return state.groups;
  },
};

const actions = {
  setGroups({ commit }, payload) {
    const setGroupsResponse = commit("setGroups", payload);
    return setGroupsResponse;
  },
};

const mutations = {
  setGroups: (state, value) => (state.groups = value),
};

export default {
  state,
  getters,
  actions,
  mutations,
};
