<script>
import { mapActions } from "vuex";

export default {
  data() {
    return {
      companyName: process.env.VUE_APP_COMPANY_NAME,
      showSignInGuest: false
    };
  },
  methods: {
    ...mapActions(["authUser"]),

    signInGuest() {
      let payload = {
        identity: "nws9987",
        email_addr: "demo@nationwilcox.co.uk",
        password: "pass"
      };

      this.authUser(payload)
        .then(response => {
          console.log(response);
          if (response == true) {
            this.$router.push({ name: "Home" });
          } else {
            console.log("-- Auth Error");
          }
        })
        .catch(e => {
          console.log(e);
        });
    }
  },
  computed: {
    logo() {
      return require(process.env.VUE_APP_LOGO_MAIN);
    },

    theme() {
      return this.$vuetify.theme.dark ? "dark" : "light";
    },

    style() {
      const Style = {
        background: this.$vuetify.theme.themes[this.theme].background,
        color: this.$vuetify.theme.themes[this.theme].color
      };

      return Style;
    },

    cncEnabled() {
      return this.$store.getters.retrieveCncOrdersEnabled;
    }
  }
};
</script>

