const state = {
  darkTheme: false,
  productGrid: true,
  productCountry: false,
  productCode: true,
};

const getters = {
  retrieveSettings: (state) => {
    return state;
  },
  retrieveDarkTheme: (state) => {
    return state.darkTheme;
  },

  retrieveProductGrid: (state) => {
    return state.productGrid;
  },

  retrieveProductCode: (state) => {
    return state.productCode;
  },

  retrieveProductCountry: (state) => {
    return state.productCountry;
  },
};

const actions = {
  setSettings({ commit }, payload) {
    const setSettingsResponse = commit("setSettings", payload);
    return setSettingsResponse;
  },

  clearSettings({ commit }) {
    const setSettingsResponse = commit("setSettings", null);
    return setSettingsResponse;
  },

  setDarkTheme({ commit }, payload) {
    const setSettingsResponse = commit("setDarkTheme", payload);
    return setSettingsResponse;
  },

  setProductGrid({ commit }, payload) {
    const setSettingsResponse = commit("setProductGrid", payload);
    return setSettingsResponse;
  },

  setProductCode({ commit }, payload) {
    const setSettingsResponse = commit("setProductCode", payload);
    return setSettingsResponse;
  },

  setProductCountry({ commit }, payload) {
    const setSettingsResponse = commit("setProductCountryCode", payload);
    return setSettingsResponse;
  },
};

const mutations = {
  setSettings: (state, settings) => (state = settings),
  setDarkTheme: (state, value) => (state.darkTheme = value),
  setProductGrid: (state, value) => (state.productGrid = value),
  setProductCode: (state, value) => (state.productCode = value),
  setProductCountryCode: (state, value) => (state.productCountry = value),
};

export default {
  state,
  getters,
  actions,
  mutations,
};
