<template>
  <div>
    <v-app-bar app clipped-left flat :style="appBarStyle">
      <v-app-bar-nav-icon @click="showMenuDrawer = true" class="hidden-lg-and-up"></v-app-bar-nav-icon>

      <v-toolbar-title color="primary">{{companyName}}</v-toolbar-title>
      <v-spacer />

      <v-tooltip v-if="trollyItems" class="hidden-md-and-down" bottom>
        <template v-slot:activator="{ on }">
          <span class="mr-2" v-on="on">{{trollyTotal}}</span>
        </template>
        <span>Basket Value</span>
      </v-tooltip>

      <v-tooltip bottom>
        <template v-slot:activator="{ on }">
          <v-btn
            v-on="on"
            color="primary darken-1"
            icon
            @click="dialog = false"
            :to="{name: 'OrdersBasket'}"
          >
            <v-badge v-if="trollyItems" color="orange darken-2" overlap :content="trollyItems">
              <v-icon>shopping_cart</v-icon>
            </v-badge>
            <v-icon v-else>shopping_cart</v-icon>
          </v-btn>
        </template>
        <span>View Basket</span>
      </v-tooltip>

      <v-tooltip
        v-if="$store.getters.retrieveIsCashCustomer ||
        $store.getters.retrieveIsCncCustomer"
        bottom
        class="mr-2 hidden-md-and-down"
      >
        <template v-slot:activator="{ on }">
          <v-btn v-on="on" color="primary darken-1" icon class="hidden-md-and-down">
            <span v-if="$store.getters.retrieveIsCashCustomer">D</span>
            <span v-else>C</span>
          </v-btn>
        </template>
        <span v-if="$store.getters.retrieveIsCashCustomer">Delivery Order</span>
        <span v-else>Colection Order</span>
      </v-tooltip>

      <v-dialog v-model="dialog" persistent max-width="290">
        <template v-slot:activator="{ on }">
          <div>
            <v-btn
              color="primary"
              outlined
              dark
              block
              v-on="on"
              class="hidden-md-and-down ml-2"
            >Sign Out</v-btn>
          </div>
        </template>

        <template v-slot:prepend v-slot:activator="{ on }">
          <v-btn color="primary" dark v-on="on">Open Dialog</v-btn>
        </template>
        <v-card>
          <v-card-title class="headline">Are you sure?</v-card-title>
          <v-card-text>You will need to sign in again to use the site.</v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="primary darken-1" text @click="dialog = false">No</v-btn>
            <v-btn color="primary" @click="dialog = false, signOut()">Yes</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-app-bar>

    <v-navigation-drawer clipped app v-model="showMenuDrawer" :style="naviationStyle">
      <v-dialog v-model="dialog" persistent max-width="290">
        <template v-slot:activator="{ on }">
          <div class="hidden-lg-and-up pa-2">
            <v-btn color="primary" dark block v-on="on">Sign Out</v-btn>
          </div>
        </template>

        <template v-slot:prepend v-slot:activator="{ on }">
          <v-btn color="primary" dark v-on="on">Open Dialog</v-btn>
        </template>
        <v-card>
          <v-card-title class="headline">Are you sure?</v-card-title>
          <v-card-text>You will need to sign in again to use the site.</v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="primary darken-1" text @click="dialog = false">No</v-btn>
            <v-btn color="primary" @click="dialog = false, signOut()">Yes</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <v-list nav dense>
        <v-list-item-group exact-active-class="primary--text text--accent-4">
          <v-list-item :to="{name: 'Home'}" exact exact-active-class="primary--text text--accent-4">
            <v-icon :color="menu_icon" class="mr-2">mdi-home</v-icon>
            <v-list-item-title>Home</v-list-item-title>
          </v-list-item>
        </v-list-item-group>

        <div v-for="(mainItem, index) in mainItems" :key="index" style="margin-top:16px">
          <v-list-item-group
            active-class="primary--text text--accent-4"
            v-if="checkShow(mainItem)"
            :color="mainItem.color"
          >
            <v-subheader>{{mainItem.title}}</v-subheader>
            <div v-for="(item, index) in mainItem.subItems" :key="index">
              <div v-if="checkShow(item)">
                <v-list-item :to="item.link" exact-active-class inset :color="menu_color">
                  <v-icon :color="menu_icon" left>{{ item.icon }}</v-icon>
                  <v-list-item-title>{{ item.title }}</v-list-item-title>
                </v-list-item>
              </div>
            </div>
          </v-list-item-group>
        </div>

        <v-list-item-group style="margin-top:16px">
          <v-subheader>Others</v-subheader>
          <v-list-item
            :to="{name: 'FAQ'}"
            :color="menu_color"
            active-class="primary--text text--accent-4"
          >
            <v-icon left :color="menu_icon">mdi-comment-question-outline</v-icon>
            <v-list-item-title>FAQ</v-list-item-title>
          </v-list-item>
          <v-list-item
            :to="{name: 'UserAccount'}"
            :color="menu_color"
            active-class="primary--text text--accent-4"
          >
            <v-icon left :color="menu_icon">mdi-account-circle-outline</v-icon>
            <v-list-item-title>Account</v-list-item-title>
          </v-list-item>

          <v-list-item
            :to="{name: 'UserSettings'}"
            :color="menu_color"
            active-class="primary--text text--accent-4"
          >
            <v-icon left :color="menu_icon">settings</v-icon>
            <v-list-item-title>Settings</v-list-item-title>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>
  </div>
</template>

<script>
import { mapActions } from "vuex";

export default {
  name: "MainMenu",
  components: {},
  data() {
    return {
      companyName: process.env.VUE_APP_COMPANY_NAME,
      showMenuDrawer: true,
      dialog: false
    };
  },
  beforeMount() {
    this.checkDrawer();
  },
  methods: {
    ...mapActions(["clearUser", "clearCustomer", "clearFAQ", "clearOrders"]),

    checkDrawer() {
      this.isMobile = !this.$vuetify.breakpoint.lgAndUp;
      this.showMenuDrawer = !this.isMobile;
    },

    checkShow(item) {
      let showItem = false;

      switch (parseInt(item.show)) {
        case 0:
          showItem = false;
          break;
        case 1:
          showItem = true;
          break;
        case 2:
          console.log("-- 2");

          if (
            this.$store.getters.retrieveIsCashCustomer ||
            this.$store.getters.retrieveIsCncCustomer
          ) {
            showItem = true;
          } else {
            showItem = false;
          }
          break;
        case 3:
          if (
            this.$store.getters.retrieveIsCashCustomer ||
            this.$store.getters.retrieveIsCncCustomer
          ) {
            showItem = false;
          } else {
            showItem = true;
          }
          break;
        default:
          showItem = false;
          break;
      }

      return showItem;
    },

    signOut() {
      //Clear our user here once done re-direct us out
      this.clearCustomer();
      this.clearUser();
      this.clearFAQ();
      this.clearOrders();
      this.dialog = false;
      this.$router.push({ name: "SignIn" });
    }
  },
  computed: {
    mainItems() {
      return this.$store.getters.retrieveMenuOptions;
    },

    theme() {
      return this.$vuetify.theme.dark ? "dark" : "light";
    },

    menu_color() {
      return this.$vuetify.theme.themes[this.theme].menu_color;
    },

    menu_icon() {
      return this.$vuetify.theme.themes[this.theme].menu_icon;
    },

    appBarStyle() {
      const Style = {
        background: this.$vuetify.theme.themes[this.theme].background_alt,
        "border-width": "0 0 thin",
        "border-style": "solid",
        "border-color": this.$vuetify.theme.themes[this.theme].menu_border_color
      };
      return Style;
    },

    naviationStyle() {
      const Style = {
        "z-index": 5,
        background: this.$vuetify.theme.themes[this.theme].background
      };
      return Style;
    },

    trolly() {
      return this.$store.getters.retrieveCurrentTrolly;
    },

    trollyItems() {
      if (this.trolly) {
        return this.$store.getters.retrieveCurrentTrolly.totalItems;
      } else {
        return false;
      }
    },

    trollyTotal() {
      if (this.trolly) {
        return this.$store.getters.retrieveCurrentTrolly.totalValue;
      } else {
        return false;
      }
    }
  }
};
</script>



<style lang="sass" scoped>
@import "~vuetify/src/styles/styles.sass"

.v-list
  border-radius: 0
</style>
