//Route protection checking
// -- If our route requires authentication then ensure we're logged in before we let the users go there
router.beforeEach((to, from, next) => {
  if (to.matched.some((record) => record.meta.requiresAuth)) {
    // This route requires auth, check our user to see if we're authed
    // -- First check if the user even exists
    if (store.getters.retrieveUser != null) {
      // The user does exist so check if we're authed
      if (store.getters.retrieveUser.auth) {
        //We are authed so carry on!
        next();

        //Get our essential info here IF it doesn't already exist
        if (store.getters.retrieveCustomer == null) {
          store
            .dispatch("getCustomer")
            .then((response) => {
              console.log("-- Get Customer Response");
              console.log(response);

              if (response.success != true) {
                console.log("Error has occured");
                console.log(response);
                // this.loadingCustomer = false;

                store.dispatch("setSnackbar", {
                  color: "error",
                  text: "Unable to load your customer information",
                });
              }
            })
            .catch((er) => {
              console.log(er);
            });
        }
      } else {
        //We are not authed so go to the SignIn page
        next({ name: "SignIn" });
      }
    } else {
      // Our user does not exist, so previously logged out or are a fresh user
      // -- Send to the SignIn page
      next({ name: "SignIn" });
    }
  } else {
    // Route does not require an auth so allow the user to view it
    // -- This is in case of things like SignIn and registration pages
    next();
  }
});

export default router;
