import axios from "axios";
import moment from "moment";

const state = {
  selectedTrolly: 0,
  trollys: [],
  orderBeingPaidFor: null,
};

const getters = {
  retrieveOrder: (state) => {
    return state;
  },

  retrieveSelectedTrolly: (state) => {
    return state.selectedTrolly;
  },

  retrieveAllTrollys: (state) => {
    return state.trollys;
  },

  retrieveCurrentTrolly: (state) => {
    let trolly = false;

    if (state.trollys[state.selectedTrolly]) {
      trolly = state.trollys[state.selectedTrolly];

      trolly.order_date_display = moment(trolly.order_date).format(
        "dddd, Do MMMM YYYY"
      );

      let trollyValue = 0;
      let totalQty = 0;

      Object.keys(trolly.trolly_items).forEach(function(key) {
        let itemValue = 0;
        totalQty += parseFloat(trolly.trolly_items[key].qty);

        switch (trolly.trolly_items[key].qty_type) {
          case "B":
            itemValue =
              trolly.trolly_items[key].box_price *
              parseFloat(trolly.trolly_items[key].qty);

            trolly.trolly_items[key].itemType = "Box";
            trolly.trolly_items[key].itemPrice = parseFloat(
              trolly.trolly_items[key].box_price
            ).toFixed(2);
            trolly.trolly_items[key].itemValue = parseFloat(itemValue).toFixed(
              2
            );
            trollyValue += itemValue;
            break;
          case "K":
            itemValue =
              trolly.trolly_items[key].kilo_price *
              parseFloat(trolly.trolly_items[key].qty);

            trolly.trolly_items[key].itemType = "Kilo";
            trolly.trolly_items[key].itemPrice = parseFloat(
              trolly.trolly_items[key].kilo_price
            ).toFixed(2);
            trolly.trolly_items[key].itemValue = parseFloat(itemValue).toFixed(
              2
            );
            trollyValue += itemValue;
            break;
          case "E":
            itemValue =
              trolly.trolly_items[key].each_price *
              parseFloat(trolly.trolly_items[key].qty);

            trolly.trolly_items[key].itemType = "Each";
            trolly.trolly_items[key].itemPrice = parseFloat(
              trolly.trolly_items[key].each_price
            ).toFixed(2);
            trolly.trolly_items[key].itemValue = parseFloat(itemValue).toFixed(
              2
            );
            trollyValue += itemValue;
            break;
        }
      });

      trollyValue = parseFloat(trollyValue).toFixed(2);
      trolly.totalValue = trollyValue;
      trolly.totalQty = totalQty;
      trolly.totalItems = Object.keys(trolly.trolly_items).length;
    }

    return trolly;
  },
  retrieveOrderBeingPaidFor: (state) => {
    if (state.orderBeingPaidFor) {
      return state.orderBeingPaidFor;
    }
  },
};

const actions = {
  clearOrder({ commit }) {
    //Revert our order state back to it's default
    const clearOrderResponse = commit("setOrder", {
      orderDate: null,
      custRef: "",
      specialInstructions: "",
      id: null,
    });
    return clearOrderResponse;
  },

  async getTrollys({ commit }) {
    let payload = {
      userId: this.getters.retrieveUser.userNum,
      sessionKey: this.getters.retrieveUser.sessionKey,
    };
    let stringPayload = JSON.stringify(payload);
    const params = new URLSearchParams();
    params.append("params", stringPayload);

    const response = await axios.post("get-trollys", params);

    if (response.data.success == true) {
      console.log("-- Trollies Response Data");
      console.log(response.data.trolly[0]);
      commit("setTrollies", response.data);
      return response.data.success;
    } else {
      response.data.success;
    }
  },

  async createNewTrolly({ commit }, orderInfo) {
    let payload = {
      userId: this.getters.retrieveUser.userNum,
      sessionKey: this.getters.retrieveUser.sessionKey,
      orderDate: orderInfo.orderDate,
      custRef: orderInfo.custRef,
    };

    let stringPayload = JSON.stringify(payload);
    const params = new URLSearchParams();
    params.append("params", stringPayload);

    const response = await axios.post("create-new-trolly", params);

    if (response.data.success == true) {
      //commit("setTrollies", response.data);
      return response.data.success;
    } else {
      response.data.success;
    }
  },

  async updateTrollyOrderDate({ commit }, orderInfo) {
    let payload = {
      userId: this.getters.retrieveUser.userNum,
      sessionKey: this.getters.retrieveUser.sessionKey,
      trollyId: this.getters.retrieveCurrentTrolly.id,
      orderDate: orderInfo.orderDate,
    };

    let stringPayload = JSON.stringify(payload);
    const params = new URLSearchParams();
    params.append("params", stringPayload);

    const response = await axios.post("trolly-update-order-date", params);

    if (response.data.success == true) {
      return response.data.success;
    } else {
      return response.data.success;
    }
  },

  async updateTrollyCustRef({ commit }, orderInfo) {
    let payload = {
      userId: this.getters.retrieveUser.userNum,
      sessionKey: this.getters.retrieveUser.sessionKey,
      trollyId: this.getters.retrieveCurrentTrolly.id,
      custRef: orderInfo.custRef,
    };

    let stringPayload = JSON.stringify(payload);
    const params = new URLSearchParams();
    params.append("params", stringPayload);

    const response = await axios.post("trolly-update-cust-ref", params);

    if (response.data.success == true) {
      return response.data.success;
    } else {
      return response.data.success;
    }
  },

  orderBeingPaidForAction({ commit }, orderInfo) {
    commit("setOrderBeingPaidFor", orderInfo);
    //console.log(orderInfo)
  },
  clearOrderBeingPaidFor({ commit }) {
    commit("setOrderBeingPaidFor", null);
  },
  async stripePaymentIntentAction({ commit }, payload) {
    let stringPayload = JSON.stringify(payload);
    const params = new URLSearchParams();
    params.append("params", stringPayload);
    const response = await axios.post("stripe-payment-intent", params);
    return response.data;
  },
  async stripeConfirmCardPayment({ commit }, payload) {
    let stringPayload = JSON.stringify(payload);
    const params = new URLSearchParams();
    params.append("params", stringPayload);
    const response = await axios.post("stripe-confirm-card-payment", params);
    return response.data;
  },
};

const mutations = {
  setTrollies: (state, value) => (state.trollys = value.trolly),
  setOrderBeingPaidFor: (state, orderDetails) =>
    (state.orderBeingPaidFor = orderDetails),
};

export default {
  state,
  getters,
  actions,
  mutations,
};
