import axios from "axios";

const state = {
  customer: null,
};

const getters = {
  retrieveCustomer: (state) => {
    return state.customer;
  },
  retrieveCustomerAddress: (state) => {
    if (state.customer) {
      return {
        name: state.customer.customer.name,

        address_line_1: state.customer.customer.address_line_1,
        address_line_2: state.customer.customer.address_line_2,
        address_line_3: state.customer.customer.address_line_3,
        address_line_4: state.customer.customer.address_line_4,

        postcode: state.customer.customer.postcode,
        telephone_no: state.customer.customer.telephone_no,
      };
    }
  },
  retrieveIsCashCustomer: (state) => {
    let retrieveIsCashCustomer = false;

    if (state.customer != null) {
      if (parseInt(state.customer.customer.delivery_account_no) == 1) {
        retrieveIsCashCustomer = true;
      }
    }

    return retrieveIsCashCustomer;
  },
  retrieveIsCncCustomer: (state) => {
    let retrieveIsCncCustomer = false;

    if (state.customer != null) {
      if (parseInt(state.customer.customer.delivery_account_no) == 3) {
        retrieveIsCncCustomer = true;
      }
    }

    return retrieveIsCncCustomer;
  },

  retrieveIsPaymentCustomer: (state) => {
    let retrieveIsCncCustomer = false;

    if (state.customer != null) {
      if (parseInt(state.customer.customer.acc_type) == 5) {
        retrieveIsCncCustomer = true;
      }
    }

    return retrieveIsCncCustomer;
  },

  retrieveAllAvailableDays: (state) => {
    let allAvailableDays = [];

    if (state.customer != null) {
      if (state.customer.allAvailableDays.length > 0) {
        allAvailableDays = state.customer.allAvailableDays;
      }
    }

    return allAvailableDays;
  },
  retrieveCustomerDeilveryDays: (state) => {
    if (state.customer) {
      let deliveryDays = {
        deliveryDayMonday: state.customer.customer.delivery_day_monday,
        deliveryDayTuesday: state.customer.customer.delivery_day_tuesday,
        deliveryDayWednesday: state.customer.customer.delivery_day_wednesday,
        deliveryDayThursday: state.customer.customer.delivery_day_thursday,
        deliveryDayFriday: state.customer.customer.delivery_day_friday,
        deliveryDaySaturday: state.customer.customer.delivery_day_saturday,
        deliveryDaySunday: state.customer.customer.delivery_day_sunday,
      };
      return deliveryDays;
    }
  },
};

const actions = {
  async getCustomer({ commit }) {
    let payload = {
      userId: this.getters.retrieveUser.userNum,
      sessionKey: this.getters.retrieveUser.sessionKey,
    };
    let stringPayload = JSON.stringify(payload);
    const params = new URLSearchParams();
    params.append("params", stringPayload);

    const response = await axios.post("get-customer", params);

    if (response.data.success == true) {
      commit("setCustomer", response.data);
    }

    console.log("-- Get Customer Store");
    console.log(response.data);
    return response.data;
  },

  clearCustomer({ commit }) {
    commit("setCustomer", null);
  },
};

const mutations = {
  //Assign the object we got from the API to be our customer
  setCustomer: (state, customer) => (state.customer = customer),
};

export default {
  state,
  getters,
  actions,
  mutations,
};
