import Vue from "vue";
import Vuex from "vuex";
import "es6-promise/auto";
import VuexPersistence from "vuex-persist";

//Load our modules here
// -- A module is an individual JS file where we create and store data
//     -- Start with our general settings
import settings from "./modules/settings";

//    -- Now our API calls (the results of which we'll store)
import configs from "./modules/configs";
import user from "./modules/user";
import customer from "./modules/customer";
import faq from "./modules/faq";
import ordersHistory from "./modules/ordersHistory";
import groups from "./modules/groups";

//    -- Our order settings
import order from "./modules/order";

Vue.use(Vuex);

const vuexLocal = new VuexPersistence({
  storage: window.localStorage,
});

export default new Vuex.Store({
  state: {
    snackbar: {},
  },
  mutations: {
    SET_SNACKBAR(state, snackbar) {
      state.snackbar = snackbar;
    },
  },
  actions: {
    setSnackbar({ commit }, snackbar) {
      snackbar.showing = true;
      snackbar.color = snackbar.color || "success";
      commit("SET_SNACKBAR", snackbar);
    },
  },
  modules: {
    settings,
    configs,
    user,
    customer,
    faq,
    order,
    ordersHistory,
    groups,
  },
  plugins: [vuexLocal.plugin],
});
