<template>
  <div>
    <v-banner :style="style" app v-model="showAnnouncement" transition="slide-y-transition">
      {{announcementMessage}}
      <template v-slot:actions="{ dismiss }">
        <v-btn class="hidden-lg-and-up" text color="white" @click="dismiss">Dismiss</v-btn>
      </template>
    </v-banner>
  </div>
</template>

<script>
export default {
  data: function() {
    return {
      showAnnouncement: this.announcementMessage
    };
  },

  computed: {
    // showAnnouncement: {
    //   get: function() {
    //     return this.show;
    //   },
    //   set: function(newValue) {
    //     this.show = newValue;
    //   }
    // },
    theme() {
      return this.$vuetify.theme.dark ? "dark" : "light";
    },
    style() {
      const Style = {
        background: this.$vuetify.theme.themes[this.theme].primary,
        color: "#FFF",
        "z-index": 0
      };
      return Style;
    },
    isCashCustomer() {
      return this.$store.getters.isCashCustomer;
    },
    announcementMessage() {
      if (this.isCashCustomer) {
        const announcement = this.$store.getters.retrieveCashAnnouncmentMessage;
        return announcement;
      } else {
        const announcement = this.$store.getters
          .retrieveAccountAnnouncmentMessage;
        return announcement;
      }
    }
  }
};
</script>

