<template>
  <v-card class="elevation-3">
    <v-toolbar color="primary" dark flat dense>
      <v-toolbar-title>Sign In</v-toolbar-title>
    </v-toolbar>
    <div class="py-0">
      <v-img :src="logo" class="my-3" contain height="140" />
    </div>

    <v-form ref="SignInForm" @submit.prevent>
      <v-row v-if="error">
        <v-card-title>
          <v-icon left color="error" class="mr-2 ml-2">error_outline</v-icon>
          <span class="error--text ml-2">Sign In Falied</span>
        </v-card-title>
        <v-card-text class="ml-3 error--text">{{errorMessage}}</v-card-text>
      </v-row>
      <v-card-text class="py-0">
        <v-text-field
          outlined
          label="Email address"
          name="email"
          prepend-inner-icon="alternate_email"
          type="email"
          v-model="email"
          :rules="emailRules"
        />

        <v-text-field
          outlined
          id="password"
          label="Password"
          name="password"
          prepend-inner-icon="lock"
          type="password"
          v-model="password"
          :rules="passwordRules"
        />
      </v-card-text>
      <v-card-actions>
        <v-btn text color="secondary" :to="{name :'Registration'}">Register Here</v-btn>
        <v-spacer />
        <v-btn large type="submit" color="primary" @click="submit">Sign In</v-btn>
      </v-card-actions>
    </v-form>
    <v-divider />
    <div class="py-2">
      <v-btn text color="secondary" :to="{name :'PasswordReset'}">Forgot Password?</v-btn>
    </div>
  </v-card>
</template>

<script>
import { mapActions } from "vuex";

export default {
  name: "SignInForm",
  data() {
    return {
      loading: true,
      transition: "scale-transition",
      email: "",
      emailRules: [
        v => !!v || "E-mail is required",
        v => /.+@.+\..+/.test(v) || "E-mail must be valid"
      ],
      password: "",
      passwordRules: [v => !!v || "Password is required"],
      showSignupMessage: false,
      error: false,
      errorMessage: ""
    };
  },
  computed: {
    logo() {
      return require(process.env.VUE_APP_LOGO_MAIN);
    },
    showMenu: {
      get: function() {
        return ["SignIn"].indexOf(this.$route.name) == -1;
      },
      // setter
      set: function(newValue) {
        this.showMenu = newValue;
      }
    }
  },

  methods: {
    ...mapActions(["authUser"]),

    submit() {
      if (this.$refs.SignInForm.validate()) {
        let payload = {
          identity: "nws9987",
          email_addr: this.email,
          password: this.password
        };
        this.error = false;
        this.errorMessage = "";
        this.authUser(payload)
          .then(response => {
            console.log(response);
            if (response == true) {
              this.$router.push({ name: "Home" });
            } else {
              console.log("-- Auth Error");
              this.error = true;
              this.errorMessage = "Incorrect Username or Password";
            }
          })
          .catch(e => {
            console.log(e);
          });
      }
    }
  },
  created() {
    this.$root.$on("singup-success", signupEmail => {
      this.email = signupEmail;
    });
  }
};
</script>
