import axios from "axios";
import store from "@/store";

import colors from "vuetify/lib/util/colors";
import vuetify from "../../plugins/vuetify";

const state = {
  //Set any/all variables we'll need before our configs are loaded in
  // -- This is just to ensure we can render our components correctly
  primary_bg: colors.blue.darken2,
  primary_menu_color: colors.blue.darken2,
  primary_menu_icon: colors.blue.darken2,

  // background: colors.grey.darken4,
  // background_alt: colors.shades.white,
};

const getters = {
  retrieveAllConfigs: (state) => {
    return state;
  },
  retrieveStripeClientKey: (state) => {
    return state.stripe_client_key;
  },
  retrieveCncOrdersEnabled: (state) => {
    if (state.orders_cnc_enabled == "1") {
      return true;
    } else {
      return false;
    }
  },

  retrieveCncAddress: (state) => {
    if (state.cnc_postcode) {
      return {
        address_line_1: state.cnc_address_line_1,
        address_line_2: state.cnc_address_line_2,
        address_line_3: state.cnc_address_line_3,
        address_line_4: state.cnc_address_line_4,
        postcode: state.cnc_postcode,
        telephone_no: state.cnc_telephone,
      };
    }
  },

  retrieveAccountAnnouncmentMessage: (state) => {
    let announcementMessage = null;

    if (state.announcement_message) {
      announcementMessage = state.announcement_message;
    }

    return announcementMessage;
  },
  retrieveCashAnnouncmentMessage: (state) => {
    let announcementMessage = null;

    if (state.cash_user_announcement_message) {
      announcementMessage = state.cash_user_announcement_message;
    }

    return announcementMessage;
  },

  retrieveCompanyHomePageText: (state) => {
    let companyHomePageText = "No Message Found";

    if (state.company_home_page_text) {
      companyHomePageText = state.company_home_page_text;
    }

    return companyHomePageText;
  },

  retrieveMinimumOrderValue: (state) => {
    let minimumOrderValue = 0;

    if (
      store.getters.retrieveIsCashCustomer ||
      store.getters.retrieveIsCncCustomer
    ) {
      if (state.cash_user_min_order_value) {
        minimumOrderValue = parseFloat(state.cash_user_min_order_value);
      }
    }

    //cash_user_min_order_value

    return minimumOrderValue;
  },

  retrieveCutOffTime: (state) => {
    let cutOffHour = 23;

    if (store.getters.retrieveIsCashCustomer) {
      if (state.orders_cash_next_day_cutoff_time_final) {
        cutOffHour = parseInt(state.orders_cash_next_day_cutoff_time_final);
      } else {
        cutOffHour = 16;
      }
    } else if (store.getters.retrieveIsCncCustomer) {
      if (state.orders_cnc_next_day_cutoff_time_final) {
        cutOffHour = parseInt(state.orders_cnc_next_day_cutoff_time_final);
      } else {
        cutOffHour = 16;
      }
    } else {
      if (state.orders_next_day_cutoff_time_final) {
        cutOffHour = parseInt(state.orders_next_day_cutoff_time_final);
      } else {
        cutOffHour = 23;
      }
    }

    //cash_user_min_order_value

    return cutOffHour;
  },
  retrieveCutOffTimeWarning: (state) => {
    return state.orders_next_day_cutoff_time_warning;
  },

  retrievePrimaryBg: (state) => {
    return state.primary_bg;
  },

  retrieveBackgroundAlt: (state) => {
    return state.background_alt;
  },

  retrieveMenuColor: (state) => {
    return state.menu_color;
  },
  retrieveMenuIcon: (state) => {
    return state.menu_icon;
  },

  retrieveMenuOptions: (state) => {
    let menuOptions = {
      Orders: {
        show: 1,
        title: "Orders",
        icon: "mdi-credit-card-outline",
        color: "success",
        subItems: {
          Create: {
            show: 1,
            title: "Create Order",
            icon: "mdi-file-document-edit-outline",
            link: "/orders/create",
          },
          Template: {
            show: 1,
            title: "Template Orders",
            icon: "mdi-format-list-checkbox",
            link: "/orders/template",
          },
          View: {
            show: 1,
            title: "Your Orders",
            icon: "mdi-card-search-outline",
            link: "/orders/view",
          },
        },
      },
      Accounts: {
        show: 0,
        title: "Accounts",
        icon: "mdi-trending-up",
        color: "primary",
        subItems: {
          Invoice: {
            show: 0,
            title: "Copy Invoice",
            icon: "mdi-clipboard-file-outline",
            link: "/accounts/invoice",
          },
          Statement: {
            show: 0,
            title: "Copy Statement",
            icon: "mdi-content-copy",
            link: "/accounts/statement",
          },
        },
      },
      Reports: {
        show: 0,
        title: "Reports",
        icon: "mdi-file-chart-outline",
        color: "purple",
        subItems: {
          Sales: {
            show: 0,
            title: "Product Sales",
            icon: "mdi-chart-bar",
            link: "/reports/sales",
          },
          Allergen: {
            show: 0,
            title: "Allergen Report",
            icon: "mdi-alert",
            link: "/reports/allergen",
          },
          Nutrition: {
            show: 0,
            title: "Nutrition Information",
            icon: "mdi-alert-circle-outline ",
            link: "/reports/nutrition",
          },
        },
      },
    };

    //Override our Accounts Options here

    //Orders has sub options only
    if (state.orders_create_order_show) {
      menuOptions.Orders.subItems.Create.show = parseInt(
        state.orders_create_order_show
      );
    }

    if (
      store.getters.retrieveIsCashCustomer ||
      store.getters.retrieveIsCncCustomer
    ) {
      menuOptions.Orders.subItems.Template.show = 0;
    } else {
      if (state.orders_template_orders_show) {
        menuOptions.Orders.subItems.Template.show = parseInt(
          state.orders_template_orders_show
        );
      }
    }

    if (state.orders_your_order_show) {
      menuOptions.Orders.subItems.View.show = parseInt(
        state.orders_your_order_show
      );
    }

    // -- Main Option
    console.log("== Checking Account Menu ==");
    console.log(store.getters.retrieveIsCashCustomer);
    console.log(store.getters.retrieveIsCncCustomer);

    if (
      store.getters.retrieveIsCashCustomer ||
      store.getters.retrieveIsCncCustomer
    ) {
      console.log("-- Hiding Account Menu");
      menuOptions.Accounts.show = 0;
    } else {
      if (state.accounts_menu_show) {
        menuOptions.Accounts.show = parseInt(state.accounts_menu_show);
      }
    }
    // -- Sub Options
    if (state.accounts_copy_invoice_show) {
      menuOptions.Accounts.subItems.Invoice.show = parseInt(
        state.accounts_copy_invoice_show
      );
    }
    if (state.accounts_copy_statement_show) {
      menuOptions.Accounts.subItems.Statement.show = parseInt(
        state.accounts_copy_statement_show
      );
    }

    //Override our Reports Options here
    // -- Main Option
    if (
      store.getters.retrieveIsCashCustomer ||
      store.getters.retrieveIsCncCustomer
    ) {
      console.log("-- Hiding Account Menu");
      menuOptions.Reports.show = 0;
    } else {
      if (state.reports_menu_show) {
        menuOptions.Reports.show = parseInt(state.reports_menu_show);
      }
    }

    // -- Sub Options
    if (state.reports_product_sales_show) {
      menuOptions.Reports.subItems.Sales.show = parseInt(
        state.reports_product_sales_show
      );
    }
    if (state.reports_allergen_show) {
      menuOptions.Reports.subItems.Allergen.show = parseInt(
        state.reports_allergen_show
      );
    }
    if (state.reports_allergen_show) {
      menuOptions.Reports.subItems.Nutrition.show = parseInt(
        state.reports_allergen_show
      );
    }

    return menuOptions;
  },
};

const actions = {
  async getConfigs({ commit }) {
    await axios
      .get("get-config", {
        params: {
          params: {
            group: "cpgo",
            identity: "nws9987",
          },
        },
      })
      .then((response) => {
        if (response.data.success) {
          commit("setConfigs", response.data.config);
        }
      })
      .catch((err) => {
        console.log(err);
      });
  },
};

const mutations = {
  setConfigs: (state, configs) => {
    configs.forEach((config) => {
      const var_name = config.var_name;
      state[var_name] = config.var_value;
    });

    vuetify.framework.theme.themes.light.primary = state.css_primary_bg;
    vuetify.framework.theme.themes.dark.primary = state.css_primary_bg;

    // vuetify.framework.theme.themes.light.background_alt =
    //   state.css_primary_bg_alt;
    // vuetify.framework.theme.themes.dark.background_alt = colors.grey.darken4;

    vuetify.framework.theme.themes.light.menu_color =
      state.css_menu_button_text;
    vuetify.framework.theme.themes.dark.menu_color = colors.grey.lighten5;

    vuetify.framework.theme.themes.light.menu_icon = state.css_menu_button_icon;
    vuetify.framework.theme.themes.dark.menu_icon = colors.grey.lighten3;

    return;
  },
};

export default {
  state,
  getters,
  actions,
  mutations,
};
